<template>
  <div id="select">
      <ul>
          <li v-for="(item,index) in data" :key="index" @click="detail(item.id)">
              <div class="content-top" >
                    <p :class="{row:false}">{{item.name}}</p>
                    <div class="img-box-1" v-if="item.img1 && !item.img3">
                        <img :src="item.img1" alt="">
                    </div>
                    <div class="img-box-3" v-else-if="item.img3">
                        <img :src="item.img1" alt="">
                        <img :src="item.img2" alt="">
                        <img :src="item.img3" alt="">
                    </div>
              </div>
              <div class="content-bottom">
                  <p>
                      <span class="author">{{item.author}}</span>
                      <span class="comment">评论</span>{{item.comment}}
                  </p>
              </div>
          </li>
      </ul>
  </div>
</template>

<script>
export default {
    name: 'Select',
    props: {
        data: Array
    },
    methods: {
        detail(id) {
            this.$router.push({ path: `/detail/${id}` })
        }
    },
    created(){
    }
}
</script>

<style lang='less' scoped>
    ul{
        list-style: none;
        padding: 0 20px;
        margin: 0;
        li{
            text-align: start;
            padding: 5px 0;
            border-bottom: .2px solid rgba(204, 204, 204, 0.767);
            .content-top{
                    display: flex;
                    flex-wrap: wrap;
                    padding-top: 5px;
                p{
                    padding-top: 5px;
                    box-sizing: border-box;
                    flex: 1;
                    margin: 0;
                    font-size: 1.1em;
                    text-align: start;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 3;
                    line-height: 1.1em;
                }
                .img-box-1{
                    height: 4rem;
                    width: 36%;
                    img{
                        padding-left: 10px;
                        box-sizing: border-box;
                        height: 100%;
                        width: 100%;
                    }
                }
                .img-box-3{
                    display: flex;
                    justify-content: space-between;
                    height: 4rem;
                    margin-top: 5px;
                    // width: 100%;
                    img{
                        height: 100%;
                        width: 32.5%;
                    }
                }
                .row{
                    width: 100%;
                }
            }
            
            .content-bottom{
                margin-top: .5em;
                font-size: .7em;
                p{
                    margin: 0;
                    color: #999;
                    .comment{
                        padding: 0 5px;
                    }
                }
            }
        }
    }
</style>
